.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.flexContainer{
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
}
.add_todo{
  margin: 0 !important;
}
.page_header{
  display: flex;
  font-size: 22px;
  height: 70px;
  color: white;
  text-align: center;
  background: #563d7c;
  align-items: center;
  justify-content: center;
}
.btn.custom_btn_reset{
  text-transform: capitalize;
  border-radius: 2px;
  color: white;
  background: #ec7063;
  margin-right: 10px;
  width: 115px;
  transition: all 0.4s ease-out;
}
.btn.custom_btn_reset:hover{
  background: #dc6b5f;
  box-shadow: 2px 2px 4px #a78f8f;
  color: white;
  transform: scale(1.01);
}
.todo_container{
  padding:30px 0px;
}
.row.tag_filter_ctn{
  position: relative;
  margin: 10px 0px;
  
}
.empty_list_container{
  height: 200px;
  border: 0.25px solid #efefef;
  align-items: center;
  justify-content: center;
  margin-top: 10px;
}
.empty_list_container img{
  width: 120px;
}
.task_list_stats{
  font-size: 18px;
  margin: 10px 15px;
}
.task_list_stats .stat{
  display: inline-block;
    padding: 6px 15px;
    margin-right: 12px;
    font-size: 13px;
    border-radius: 5px;
}
.task_list_stats .all_tasks{
  background: #d9f8ff;
    border: 0.5px solid #69e3ff;
}
.task_list_stats .pending_tasks{
  background: #fff8ea;
  border: 0.5px solid #f5d199
}
.task_list_stats .completed_tasks{
  background: #e3ffe7;
  border: 0.5px solid #80ff92;
}
.render_list .todo_item_pending{
  background: #fff8ea;
  cursor: pointer;
  border-radius: 0 !important;
}
.render_list .todo_item_pending:hover{
  background-color: #b2e0b2;
}
.render_list .todo_item_completed{
  cursor: pointer;
  background: #e3ffe7;
  border-radius: 0 !important;
}
.render_list .highlight_hashtag{
  color: #1da1f2;
  font-style: italic;
}
.list-group.render_list_ul{
  margin: 0 15px;
}
.App-link {
  color: #61dafb;
}
.tag{
  margin-left: 10px;
    color: white;
    display: inline-block;
    background: #f1c40f;
    border: 0.5px solid #ffc107;
    padding: 5px;
    border-radius: 33px;
    position: relative;
    top: -4px;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
